/**
 * Represents the data error constants
 */
/* tslint:disable:no-internal-module */
module DataErrorConstants {
/* tslint:enable:no-internal-module */
  'use strict';

  export const argumentException: string = '-2147024809, System.ArgumentException';
  export const unauthorizedAccessException: string = '-2147024891, System.UnauthorizedAccessException';
  export const directoryNotFoundException: string = '-2147024893, System.DirectoryNotFoundException';
  export const fileNotFoundException: string = '-2147024894, System.FileNotFoundException';
  export const argumentNullException: string = '-2147467261, System.ArgumentNullException';
  export const resourceNotFoundException: string = '-1, Microsoft.SharePoint.Client.ResourceNotFoundException';
  export const searchServiceNotFoundException: string =
    '-1, Microsoft.Office.Server.Search.Query.SearchServiceNotFoundException';
  export const httpClientError: string = 'http_server_error';
  export const httpServerError: string = 'http_server_error';
  export const jsonParseError: string = 'json_parse_error';
  export const requestTimedOut: string = 'props_timed_out';
  export const transportError: string = 'transport_error';
}

export default DataErrorConstants;
